import React, { Component } from 'react';
import _ from 'lodash';
import rawSessionData from '../data/sessions';
import rawUserData from '../data/users';
import TableItem from '../table-item/TableItem';
import './styles.css';
import ChartComponent from '../chart/Chart';

const sessionFilter = 'rsschool';

const filterSessions = session => rawSessionData.filter(
  e => _.startsWith(e.alias, session) && !_.isEmpty(e.rounds),
);
const getSessionNames = () => _.uniq(
  rawSessionData.map(e => e.alias).filter(e => _.startsWith(e, sessionFilter)),
);

class TableContainer extends Component {
  constructor(props) {
    super(props);
    this.state = { sessionNames: getSessionNames(), userData: rawUserData };
  }

  toggleSession(id) {
    this.setState({ sessionData: filterSessions(id), selectedUsers: [] });
  }

  selectUser(user, isSelected) {
    let { selectedUsers } = this.state;

    if (!selectedUsers) selectedUsers = [];

    if (isSelected) selectedUsers.push(user);
    else _.remove(selectedUsers, u => u.uid === user.uid);

    this.setState({ selectedUsers });
  }

  render() {
    const {
      selectedUsers, sessionNames, sessionData, userData,
    } = this.state;

    return (
      <div className="table">
        <div className="table__controls">
          <div>Sessions:</div>
          <ul>
            {sessionNames.map(e => (
              <li key={e}>
                <label htmlFor={e}>
                  <input
                    type="radio"
                    name="session"
                    id={e}
                    onClick={() => this.toggleSession(e)}
                  />
                  {e}
                </label>
              </li>
            ))}
          </ul>
        </div>
        <div className="table__content">
          {sessionData
            && sessionData.map(session => (
              <div key={session.game_master_id}>
                {selectedUsers ? (
                  <ChartComponent users={selectedUsers} session={session} />
                ) : null}
                <TableItem
                  users={userData}
                  session={session}
                  onUserSelect={(user, isSelected) => this.selectUser(user, isSelected)
                  }
                />
              </div>
            ))}
        </div>
      </div>
    );
  }
}

export default TableContainer;
