import React, { Component } from 'react';
import _ from 'lodash';
import PropType from 'prop-types';

import './styles.css';

function getTime(solution, timeOut) {
  if (_.isEmpty(solution)) { return timeOut; }
  const { time: { $numberLong } } = solution;
  return _.toNumber($numberLong);
}

function getCode(solution) {
  return _.isEmpty(solution)
    ? 'no solution provided'
    : solution.code;
}

class TableResultCell extends Component {
  constructor(props) {
    super(props);
    this.state = { showTooltip: false };
  }

  displayTooltip() {
    this.setState({ showTooltip: true });
  }

  hideTooltip() {
    this.setState({ showTooltip: false });
  }

  render() {
    const { solution, timeOut } = this.props;
    const { showTooltip } = this.state;
    return (
      <td onMouseEnter={() => this.displayTooltip()} onMouseLeave={() => this.hideTooltip()} className="result-cell">
        <span className="time">{getTime(solution, timeOut)}</span>
        <span className="tooltip" hidden={!showTooltip}>{getCode(solution)}</span>
      </td>
    );
  }
}

TableResultCell.propTypes = {
  solution: PropType.shape(
    PropType.object,
  ).isRequired,
  timeOut: PropType.number.isRequired,
};

export default TableResultCell;
