import React from 'react';
import PropType from 'prop-types';
import TableRow from '../table-row/TableRow';
import './styles.css';

export default function TableItem({ users, session, onUserSelect }) {
  const { puzzles, rounds, game } = session;
  const gameRounds = rounds.map(r => ({
    ...r,
    timeOut: +puzzles[r.puzzleIndex.$numberLong].options.timeLimit.$numberLong,
  }));
  return (
    <table>
      <caption>{game}</caption>
      <thead>
        <tr>
          <th>Compare</th>
          <th>Name</th>
          {puzzles.map(p => (<th key={p.name}>{p.name}</th>))}
          <th>Total Time</th>
        </tr>
      </thead>
      <tbody>
        {users.map(u => (
          <TableRow
            key={u.uid}
            user={u}
            rounds={gameRounds}
            onUserSelect={onUserSelect}
          />
        ))}
      </tbody>
    </table>
  );
}

TableItem.propTypes = {
  users: PropType.arrayOf(PropType.object).isRequired,
  session: PropType.shape({
    game: PropType.string,
    puzzles: PropType.arrayOf(PropType.shape({
      name: PropType.string,
    })),
    rounds: PropType.arrayOf(PropType.object),
  }).isRequired,
  onUserSelect: PropType.func.isRequired,
};
