import React from 'react';
import _ from 'lodash';
import PropType from 'prop-types';
import TableResultCell from '../table-result-cell/TableResultCell';
import TableCompareCell from '../table-compare-cell/TableCompareCell';

function getTime(solution, timeOut) {
  if (_.isEmpty(solution)) return timeOut;
  const {
    time: { $numberLong },
  } = solution;
  return _.toNumber($numberLong);
}

export default function TableRow({ user, rounds, onUserSelect }) {
  let sum = 0;
  function onInputChange(isSelected) {
    onUserSelect(user, isSelected);
  }

  return (
    <tr>
      <td>
        <TableCompareCell onInputChange={onInputChange} />
      </td>
      <td>{user.displayName}</td>
      {rounds.map((r) => {
        const { solutions } = r;
        const solution = solutions[user.uid];
        sum += getTime(solution, r.timeOut);
        return (
          <TableResultCell
            key={r.puzzleIndex.$numberLong}
            timeOut={r.timeOut}
            solution={solution}
          />
        );
      })}
      <td>{sum}</td>
    </tr>
  );
}

TableRow.propTypes = {
  user: PropType.shape({
    name: PropType.string,
    uid: PropType.string,
  }).isRequired,
  rounds: PropType.arrayOf(PropType.object).isRequired,
  onUserSelect: PropType.func.isRequired,
};
