import React from 'react';
import Chart from 'react-google-charts';
import _ from 'lodash';
import PropType from 'prop-types';

const getTime = (session, puzzle, puzzleIndex, uid) => {
  const { rounds } = session;
  const round = _.find(rounds, r => _.toNumber(r.puzzleIndex.$numberLong) === puzzleIndex);
  const solution = round.solutions[uid];

  if (_.isEmpty(solution)) { return _.toNumber(puzzle.options.timeLimit.$numberLong); }

  return _.toNumber(solution.time.$numberLong);
};

function ChartComponent({ users, session }) {
  if (!users || users.length === 0) { return null; }

  const options = { backgroundColor: '#ddd' };
  const data = ['Puzzle', ...users.map(u => u.displayName)];
  const plotData = session.puzzles
    .map((p, i) => [p.name, ...users.map(u => getTime(session, p, i, u.uid))]);

  plotData.unshift(data);

  return (
    <div className="chart">
      <h2 className="chart__title">
        {`${session.game} Chart`}
      </h2>
      <Chart
        height={500}
        chartType="LineChart"
        loader={<div>Loading Chart...</div>}
        data={plotData}
        options={options}
      />
    </div>
  );
}

ChartComponent.propTypes = {
  users: PropType.arrayOf(PropType.object).isRequired,
  session: PropType.shape({
    game: PropType.string,
    puzzles: PropType.arrayOf(PropType.shape({
      name: PropType.string,
    })),
    rounds: PropType.arrayOf(PropType.object),
  }).isRequired,
};

export default ChartComponent;
